		                <h3>Scope</h3>
		                <p>
		                    This Policy applies to our websites, products, services, software, or apps on which we post a direct link to this Policy or refer to within this Policy (collectively the "Services"). This Policy covers data collected through the Services and not any data collection or processing practices of other services that maintain their own, separate privacy policies. Occasionally, we refer to this Policy in relation to surveys and special-purpose web pages, for example, our Careers web pages; under such circumstances, this Policy applies as modified in the particular notice or consent request (e.g., with respect to types of data collected or purposes of collection).
		                </p>
		                <h3>Types of Data and Collection Methods</h3>
		                <pwe a="" about="" are="" asked="" below.="" choices="" collect="" collect.="" data="" described="" have="" means="" of="" personal="" provide="" the="" through="" to="" us="" variety="" we="" when="" you="">
		                    <p>
		                        <strong>Data you submit.</strong> We collect your name, email address(es), mailing address(es), telephone number(s), and data related to the business you represent when you request information from us, register,
		                        order a product or service, or otherwise intentionally send us such information. In connection with chargeable services, we will also collect payment information (e.g., credit card number and related verification
		                        data), phone number, and billing and shipping addresses.
		                    </p>
		                    <p>
		                        <strong>Data we automatically collect from our websites.</strong> When you visit one of our websites, we collect general data from your computer or mobile device such as your browser type, operating system, IP
		                        address and the domain name from which you accessed the site, and if you are accessing our site with your mobile device, type of mobile device. In addition, we collect data about how you use our websites, such as the
		                        date and time you visit the site, the areas or pages of the site that you visit, the amount of time you spend viewing the site, the number of times you return to the site and other click-stream data.
		                    </p>
		                    <p>
		                        <strong>Cookies.</strong>We use cookies, web beacons, or similar technologies to collect data about your use of our websites or web apps. A cookie is a small text file that is stored on the hard drive of your
		                        computer or device when you access the site. We use cookies to help operate our sites and services, including to: (1) remember you so you dont have to enter the same information when you return to the site, (2)
		                        provide you with customized content or offers on the site; (3) develop aggregate statistics to help monitor site performance, conduct research, improve our content and services, and (4) help protect the security of
		                        our sites and services.
		                    </p>
		                    <p>
		                        We also allow other companies such as web analytics partners, advertisers, or ad networks to set or access their cookies or web beacons (also referred to as 1x1 pixel.gifs or action tags) on the site. For example, we
		                        use third party advertising companies to serve ads on our behalf across the Internet. These third-party advertising companies employ cookies and web beacons to measure and improve the effectiveness of ads for their
		                        clients, including us. To do so, these companies use data about your visits to our site and other websites over time. This data may include: date/time of banner ad shown, a unique identifier contained in their
		                        cookie, and the IP address of your device. This data can also be used to personalize ads so that you are more likely to see ads about goods and services of interest to you. If you would like more information about
		                        this practice and your choices about personalized advertising, visit <a href="http://www.networkadvertising.org/choices/">this opt out article</a>.
		                    </p>
		                    <p>
		                        You are free to decline cookies, but by doing so, you may not be able to use some features on the site or take full advantage of our offerings. Check the "Help" menu of your browser to learn how to change your cookie
		                        preferences. For further information, please see our Cookie Policy.
		                    </p>
		                    <p>
		                        <strong>Software and apps:</strong>Qualcomm Incorporated's subsidiaries create a variety of software and applications for mobile devices that may help improve mobile device performance, conserve battery power,
		                        improve device security, or offer other benefits. Through these software applications, we may collect location data, unique identifiers (such as a chipset serial number or international subscriber ID), data about the
		                        applications installed and/or running on the device, configuration data such as the make, model, and wireless carrier, the operating system and version data, software build data, and data about the performance of the
		                        device such as performance of the chipset, battery use, and thermal data. <a href="https://www.qualcomm.com/site/privacy/services">Learn More</a>
		                    </p>
		                    <p><strong>Third Party Sources: </strong>We may also obtain personal data from third party sources such as data brokers, social networks, other partners, or public sources.</p>
		                    <h3>Collection Purposes, Use of Data</h3>
		                    <p>
		                        We collect and process personal data about you with your consent and/or as necessary to provide the products you use, operate our business, meet our contractual and legal obligations, protect the security of our
		                        systems and our customers, or fulfil other legitimate interests. The purposes for which we collect and use data include:
		                    </p>
		                    <ul>
		                        <li>To respond to the request that you sent us such as your request for information, or your request to subscribe to a service or purchase a product;</li>
		                        <li>To provide, manage, maintain, and secure the Service(s) you request;</li>
		                        <li>To operate and improve our business, including to administer, protect, and improve our services and our systems, to develop new products and services, and for other internal business purposes;</li>
		                        <li>To better understand the preferences of the users of our Services, compile aggregated statistics about usage of our Services, and help personalize your experience of our website and Services;</li>
		                        <li>To provide you with information about our technologies, product or service releases, news, and other communications; and</li>
		                        <li>Any other use we describe in this Policy or at the point where we collect the data.</li>
		                    </ul>
		                    <p>To achieve these purposes, and to the extent permitted by applicable law, we may combine the various types of data that we collect from different on and offline sources.</p>
		                    <h3>Where We Store and Process Data</h3>
		                    <p>
		                        We store and process data in the United States and on servers around in the world, including, without limitation, Asia and the European Economic Area. Wherever we store or process personal data pursuant to this
		                        Policy, we take measures designed to ensure that the data is processed in accordance with this Policy and in compliance with applicable law.
		                    </p>
		                    <p>
		                        We transfer personal data from the European Economic Area to other countries, some of which have not been determined by the European Commission to have an adequate level of data protection. When we do so, we use a
		                        variety of legal mechanisms to effectuate the transfer (such as your consent or legal contracts).
		                    </p>
		                    <h3>Disclosures of Personal Data</h3>
		                    <p>
		                        We do not sell your personal data. We do, however, share your data with Qualcomm subsidiaries. For example, we may share your request to subscribe to newsletter regarding certain products or services with the
		                        applicable Qualcomm subsidiary. We may also share your personal data with service providers who process the data on our behalf for the purposes set forth in this Policy. We may also offer training or other services
		                        to the employees and workers of our business customers. In such cases, we may share data relating to the use of our training and other services with the identified business customers for their business use. We may
		                        also share your data as required by law or in the interest of protecting or exercising our or others' legal rights, e.g., without limitation, in connection with requests from law enforcement officials and in
		                        connection with court proceedings. We may share or transfer your data in connection with a prospective or actual sale, merger, transfer or other reorganization of all or parts of our business, including related
		                        negotiations. Finally, we may also share your personal data where you have granted us permission.
		                    </p>
		                    <h3>Your Choices</h3>
		                    <p>
		                        <strong>Opt-In/Out.</strong> In some circumstances, such as email marketing, we offer you the ability to opt-in or opt-out of some kinds of data collection, use, or sharing. In such circumstances, we will respect
		                        your choice.
		                    </p>
		                    <p>
		                        <strong>Data Subject Rights.</strong> The laws of some countries or states allow you to request access, correction, deletion, restriction, or other rights with respect to your personal data.
		                        <a href="https://app.onetrust.com/app/#/webform/7ab89abb-0d42-492a-a324-0570883e2c11">Click here to exercise those rights.</a>
		                    </p>
		                    <p>
		                        <strong>Do Not Track. </strong>We do not currently respond to "Do Not Track" ("DNT") signals sent by web browsers. A uniform standard has not yet been adopted to determine how DNT signals should be interpreted and
		                        what actions should be taken by websites and third parties that receive them. However, you may use a variety of other means of controlling data collection and use, including cookie controls in your browser settings
		                        and the personalized advertising controls described in the Cookies section above.
		                    </p>
		                    <p>
		                        <strong>Non-Discrimination/Financial Incentives. </strong>The laws of some countries or states prohibit us from discriminating against you for exercising your rights described above and imposes requirements on any
		                        financial incentives offered to you related to your information. If we offer any such financial incentives, we will describe them in more detail here.
		                    </p>
		                    <h3>Retention</h3>
		                    <p>
		                        We retain personal data while your account is active, or as needed to provide you services. We will delete or de-identify personal data within a reasonable time after the data is no longer necessary for the business
		                        purpose for which it was collected. We will, however, retain and use personal data as necessary to comply with our legal obligations, resolve disputes, and enforce our agreements.
		                    </p>
		                    <h3>Security</h3>
		                    <p>
		                        Transmissions over the Internet are never 100% secure or error-free. However, we take reasonable steps to protect your data from loss, misuse, and unauthorized access, disclosure, alteration, and destruction. It is
		                        your responsibility to safeguard your passwords and User IDs and to notify us using one of the contact methods listed below if you ever suspect that your password or User ID for one of our Services has been
		                        compromised. You are solely responsible for any unauthorized use of our Services conducted via your password and User ID.
		                    </p>
		                    <h3>Children's Privacy</h3>
		                    <p>
		                        Our Services are intended for adults. We do not intentionally or knowingly collect personally-identifiable information from children as defined by applicable law and we request that children not submit any personal
		                        data to us.
		                    </p>
		                    <h3>Amendments</h3>
		                    <p>
		                        We reserve the right to change, modify, add, or remove portions of this Policy at any time, but will alert you that changes have been made by indicating on the Policy the date it was last updated. If we change the
		                        Policy in a material way, we will provide appropriate online notice to you at least thirty days in advance and/or obtain your express consent as required by law. When you visit our websites or use our Services, you
		                        are accepting the current version of this Policy. We recommend that users revisit this Policy on occasion to learn of any changes.
		                    </p>
		                    <h3>Contact Us</h3>
		                    <p>To contact us, send us an email at privacy[at]qualcomm[dot]com, or write to us at Qualcomm Incorporated, Attn. Privacy Counsel, 5775 Morehouse Drive, San Diego, CA 92121.</p>
